@charset "UTF-8";
/**
 * Capture 组件样式
 */
.z-capture, .z-capture-canvas, .z-capture-camera {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto; }

.z-capture {
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  z-index: 999;
  height: 100vh;
  width: 100%; }
  .z-capture-danger-hint {
    color: #f44336; }
  .z-capture-canvas {
    position: absolute; }
  .z-capture-camera {
    position: absolute; }
  .z-capture-operation {
    position: absolute;
    bottom: 20px;
    width: 100%;
    padding: 10px;
    box-sizing: border-box; }
